import React, { Component } from 'react';
import {
  Text,
  View,
  Button,
  TextInput,
  Picker,
  ActivityIndicator
} from 'react-native';

import Header from './Header';
import Rows from './Rows';
import QrCode from './QrCode';

const axios = require('axios');
const moment = require('moment');
const lib = require('./lib');
const db = require('./db');
const HOST = lib.HOST;

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      show:"list",
      email:"",
      weight:"",
      grade:"A",
      species:"TUNA",
      rows:[],
    }
  }

  componentDidMount() {
    db.getProfile()
    .then(profile => {
      this.setState({email:profile.email});
      return this.refreshList(profile.email);
    })
    .then(()=>{
      this.setState({show:'list'});

      this._tick = setInterval(()=>this.refresh(),1000);
    });
  }

  componentWillUnmount() {
    if(this._tick) clearInterval(this._tick);
    if(this._busyRefresh) this._busyRefresh = null;
  }

  refresh() {
    if(this._busyRefresh) return;
    this._busyRefresh = true;

    this.refreshList()
    .then(()=>{
      this._busyRefresh = false;
    });
  }

  refreshList(email) {
    if(!email) email = this.state.email;
    return this.getSelectFishesByOwner(email)
      .then(fishes=>{
        if(!fishes) return;

        function compare(a,b) {
          if (a.ownTime < b.ownTime)
            return 1;
          if (a.ownTime > b.ownTime)
            return -1;
          return 0;
        }
        
        fishes.sort(compare);

        const rows = [];
        for(let i=0;i<fishes.length;i++) {
          const fish = fishes[i];
          const fishCode = fish.fishCode;
          const species = fish.species;
          const grade = fish.grade;
          const weight = fish.weight;
          const ts = fish.ownTime;
          rows.push({
            id:fishCode,
            row1:fishCode,
            row2:species+" "+weight+grade,
            row3:moment(ts).format("DD MMM YYYY, HH:mm:ss"),
            obj:fish
          });
        }


        this.setState({rows});
      })
  }

  onClickAddFish() {
    const species = this.state.species;
    const grade = this.state.grade;
    const weight = Number(this.state.weight);
    const email = this.state.email;
    this.setState({show:'busyAdd'});
    return this.postNewFish(species,grade,weight,email)
      .then(ok=>{
        if(ok) lib.alert('Transaction success');
        else  lib.alert('Transaction fail');
        this.setState({show:'list'});
      });
  }

  showQrCode(obj) {
    this.props.history.push({
      pathname: '/qrcode',
      state: { 
        fishCode:obj.fishCode,
        email:this.state.email
      }
    })
  }

  onClickOpenWebCam() {
    this.props.history.push({
      pathname: '/qrscanner',
      state: { 
        email:this.state.email
      }
    })
  }

  render() {
    if(this.state.show === "busy") return <View style={{flex:1,alignItems:'center',justifyContent:'center'}}><ActivityIndicator /></View>;

    if(this.state.show === "qrcode") {      
      const show = this.state.qrCode;
      return <QrCode show={show} onClickBack={()=>{
        this.setState({show:'list'});
      }} />;
    }

    const email = this.state.email;
    const rows = this.state.rows;
    const msg = 'Buy fish using qr code scanner!';

    return (
      <View style={{flex:1}}>
        <Header title="SUPPLIER MENU" email={email} history={this.props.history} />
        <View style={{flex:1}}>
          <Rows rows={rows} onClickQrCode={obj=>this.showQrCode(obj)} mode="supplier" />
        </View>
        <View style={{paddingHorizontal:5,paddingVertical:20,borderTopWidth:1,flexDirection:'row'}}>
          <View style={{flex:1}}>{this.renderAddFish()}</View>
          <View style={{width:5}} />
          <View style={{flex:1}}>
            <View style={{flex:1,alignItems:'center',justifyContent:'center'}}>
              <Text style={{fontWeight:'bold',textAlign:'center'}}>{msg}</Text>
            </View>
            <Button title="SCAN" onPress={()=>this.onClickOpenWebCam()} />
          </View>          
        </View>
      </View>
    );
  }

  renderAddFish() {
    if(this.state.show === "busyAdd") {
      const h = this.state.addFishHeight;
      return <View style={{height:h,alignItems:'center',justifyContent:'center'}}><ActivityIndicator /></View>;
    }
    const enableAdd = (Number(this.state.weight) > 0);
    return (
      <View style={{}} onLayout={(event=>{
        const {height} = event.nativeEvent.layout;
        if(!this.state.addFishHeight) this.setState({addFishHeight:height});
      })}>
        <View style={{flexDirection:"row",alignItems:'center'}}>
          <Text style={{flex:1}}>Species: </Text>
          <Picker
            style={{height:40}}
            selectedValue={this.state.species}
            onValueChange={(itemValue, itemIndex) => this.setState({species: itemValue})}>
            <Picker.Item label="Tuna" value="TUNA" />
            <Picker.Item label="Mix" value="MIX" />
          </Picker>
        </View>
        <View style={{height:10}} />
        <View style={{flexDirection:"row",alignItems:'center'}}>
          <Text style={{flex:1}}>Weight: </Text>
          <TextInput
            style={{width:100,height: 40, borderColor: 'gray', borderWidth: 1}}
            onChangeText={(text) => this.setState({weight:text})}
            value={this.state.weight}
          />
        </View>
        <View style={{height:10}} />
        <View style={{flexDirection:"row",alignItems:'center'}}>
          <Text style={{flex:1}}>Grade: </Text>
          <Picker
            style={{height:40}}
            selectedValue={this.state.grade}
            onValueChange={(itemValue, itemIndex) => this.setState({grade: itemValue})}>
            <Picker.Item label="A" value="A" />
            <Picker.Item label="B" value="B" />
            <Picker.Item label="C" value="C" />
          </Picker>
        </View>
        <View style={{height:20}} />
        <Button disabled={!enableAdd} title="ADD NEW FISH" onPress={()=>this.onClickAddFish()} />
      </View>
    );
  }

  postNewFish(species,grade,weight,email) {
    const url = HOST+'/api/NewFish';
    return new Promise((resolve,reject)=>{
      axios.post(url,{
        "$class": "com.altermyth.mynetwork.NewFish",
        "species": species,
        "grade": grade,
        "weight": weight,
        "owner": email
      })
      .then(response => {
        resolve(true);
      })
      .catch(error => {
        console.log(error);
        resolve(false);
      })  
    });
  }

  getSelectFishesByOwner(email) {
    const owner = 'resource:com.altermyth.mynetwork.Supplier#'+email;
    const url = HOST+'/api/queries/selectFishesByOwner?owner='+encodeURIComponent(owner);;
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.log(error);
        resolve();
      })  
    });
  }
}