import React, { Component } from 'react';
import {
  View,
  Button,
  TextInput,
  ActivityIndicator
} from 'react-native';

import Header from './Header';
import Rows from './Rows';

const axios = require('axios');
const moment = require('moment');
const lib = require('./lib');
const HOST = lib.HOST;

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      show:"list",
      fishCode:"",
      searchTerm:"",
      rows:[],
    }
  }

  componentDidMount() {
    this._tick = setInterval(()=>this.refresh(),1000);
  }

  componentWillUnmount() {
    if(this._tick) clearInterval(this._tick);
    if(this._busyRefresh) this._busyRefresh = null;
  }

  refresh() {
    if(this._busyRefresh || this.state.fishCode.length === 0) return;
    this._busyRefresh = true;

    this.refreshList()
    .then(()=>{
      this._busyRefresh = false;
    });
  }

  refreshList() {
    const fishCode = this.state.fishCode;
    return this.getSelectLogsByFish(fishCode)
      .then(logs=>{
        if(!logs) return;

        function compare(a,b) {
          if (a.ts < b.ts)
            return 1;
          if (a.ts > b.ts)
            return -1;
          return 0;
        }
        
        logs.sort(compare);

        const rows = [];

        for(let i=0;i<logs.length;i++) {
          const obj = logs[i];
          const action = obj.action;
          const prev = (obj.prevSupplier).split('#')[1];
          const next = (obj.newSupplier).split('#')[1];;
          const ts = obj.ts;

          const row1 = action;
          let row2 = next;
          if(action === 'TRADE') row2 = prev + " => " + next;
          const row3 = moment(ts).format("DD MMM YYYY, HH:mm:ss");
          rows.push({
            id:obj.logId,
            row1:row1,
            row2:row2,
            row3:row3,
            obj:obj
          });
        }


        this.setState({rows});
      })
  }

  onClickSearch() {
    this.setState({show:'busy'});
    const code = this.state.searchTerm;
    this.getFish(code)
    .then(fish=>{
      if(!fish) {        
        lib.alert('Fish not found!');
        this.setState({show:'list'});
      } else {
        this.setState({fishCode:code,show:'list'});
      }
    });
  }

  render() {

    let list;
    if(this.state.show === "busy") list = <View style={{flex:1,alignItems:'center',justifyContent:'center'}}><ActivityIndicator /></View>;
    else {
      const rows = this.state.rows;

      list = (
        <View style={{flex:1}}>
          <Rows rows={rows} onClickQrCode={obj=>console.log(obj)} mode="tracker" />
        </View>
      );
    }


    return (
      <View style={{flex:1}}>
        <Header title="FISH TRACKER" email="Enter fish code below to track its movement" history={this.props.history} />
        <View style={{paddingHorizontal:5,paddingVertical:10,borderBottomWidth:1,flexDirection:'row'}}>
          <View style={{flex:1,flexDirection:"row",alignItems:'center'}}>
            <TextInput
              style={{flex:1,height: 40, borderColor: 'gray', borderWidth: 1}}
              onChangeText={(text) => this.setState({searchTerm:text})}
              value={this.state.searchTerm}
            />
            <View style={{width:10}} />
            <Button disabled={(this.state.searchTerm.length === 0)} title="SEARCH FISH" onPress={()=>this.onClickSearch()} />
          </View>
        </View>
        {list}
      </View>
    );
  }

  getFish(code) {
    const url = HOST+'/api/SimpleFish/'+encodeURIComponent(code);
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        console.log(response.data);
        resolve(response.data);
      })
      .catch(error => {
        console.error(error);
        resolve(null);
      })  
    });
  }

  getSelectLogsByFish(fishCode) {
    const fish = 'resource:com.altermyth.mynetwork.SimpleFish#'+fishCode;
    const url = HOST+'/api/queries/selectLogsByFish?fish='+encodeURIComponent(fish);;
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.log(error);
        resolve();
      })  
    });
  }
}