import React, { Component } from 'react';
import {
  ActivityIndicator,
  View,
  Button,
  TextInput
} from 'react-native';

import QrReader from "react-qr-reader";

const axios = require('axios');
const lib = require('./lib');
const HOST = lib.HOST;


export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      height:0,
      size:0,
      data:""      
    }
  }

  componentDidMount() {
  }

  onClickSubmit() {
    const code = this.state.data;
    this.setState({show:'process'});
    this.getFish(code)
    .then(fish=>{
      const owner = fish.owner;
      const ownerEmail = owner.split("#")[1];
      const email = this.props.location.state.email;
      return this.postTrade(fish.fishCode,ownerEmail,email);
    })
    .then(ok=>{
      if(ok) lib.alert('Transaction success');
      else  lib.alert('Transaction fail');
      this.onClickBack();
    });
  }

  onClickBack() {
    this.props.history.goBack();
  }

  processData(data) {
    if(data) {
      this.setState({data});
      lib.delay(500)
      .then(()=>this.onClickSubmit())      
    }
  }

  renderProcess() {
    return (
      <View style={{flex:1,alignItems:'center',justifyContent:'center'}}>
        <ActivityIndicator />
      </View>
    );
  }

  render() {
    if(this.state.size === 0)
      return (
        <View style={{flex:1}} onLayout={(event=>{
          const {width,height} = event.nativeEvent.layout;

          let size = width;
          if(size > height) size = height;
          this.setState({size});
        })}>
        </View>
      );
    
    // let content = null;
    // const sz = Math.floor(this.state.size * 0.95);
    // const show = this.state.data ? this.state.data : " ";

    if(this.state.show === "process") return this.renderProcess();

    return (
      <View style={{flex:1}}>
        <View style={{flex:1,alignItems:"center",justifyContent:"center"}}>
          <QrReader
            delay={300}
            onError={console.error}
            onScan={data=>this.processData(data)}
            style={{ width:this.state.size*0.8 }}
          />
        </View>
        <View style={{flexDirection:'row',padding:5,alignItems:"center",justifyContent:"center"}}>
          <TextInput
            style={{flex:1,height: 40, borderColor: 'gray', borderWidth: 1}}
            onChangeText={(text) => this.setState({data:text})}
            value={this.state.data}
          />
          <View style={{width:5}} />
          <View style={{flex:1}}>
            <Button disabled={(this.state.data.length === 0)} title="SUBMIT" onPress={()=>this.onClickSubmit()} />
          </View>
          <View style={{width:5}} />
          <View style={{flex:1}}>
            <Button title="CLOSE" onPress={()=>this.onClickBack()} />
          </View>
        </View>
      </View>
    );
  }

  getFish(code) {
    const url = HOST+'/api/SimpleFish/'+encodeURIComponent(code);
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.error(error);
        resolve(null);
      })  
    });
  }

  postTrade(fishCode,prevEmail,nextEmail) {
    const url = HOST+'/api/Trade';
    const json = {
      "$class": "com.altermyth.mynetwork.Trade",
      "fish": fishCode,
      "prevSupplier": prevEmail,
      "newSupplier": nextEmail
    };

    return new Promise((resolve,reject)=>{
      axios.post(url,json)
      .then(response => {
        resolve(true);
      })
      .catch(error => {
        console.error(error);
        resolve(false);
      })  
    });
  }
}