import PouchDB from 'pouchdb';

let DB = new PouchDB('device');

module.exports.autoLogin = autoLogin;
module.exports.login = login;
module.exports.logout = logout;
module.exports.getProfile = getProfile;

function autoLogin() {
  return retrieveDoc('profile')
    .then(doc=>{
      if(doc.email && doc.profile) {
        return doc;
      } else {
        return null;
      }
    });
}

function login(email,profile) {
  // const dbName = new Buffer(email).toString('hex');

  return retrieveDoc("profile")
  .then(doc => {
    doc.email = email;
    doc.profile = profile;
    return DB.put(doc);
  });
}

function logout() {
  return retrieveDoc("profile")
  .then(doc => {
    doc.email = null;
    doc.profile = null;
    return DB.put(doc);
  });
}

function getProfile() {
  return DB.get("profile");
}

function retrieveDoc(name) {
  return new Promise((resolve,reject)=>{
    DB.get(name)
    .then(resolve)
    .catch(err=>{
      const newObj = {"_id":name};
      DB.put(newObj)
      .then(()=>{
        return DB.get(name);
      })
      .then(resolve);
    });
  })
}