import {
  Platform,
} from 'react-native';

module.exports.delay = delay;
module.exports.alert = platformAlert;
module.exports.HOST = 'https://paketviral.herokuapp.com';


function delay(ms) {
  return new Promise((resolve, reject) => {
    setTimeout(()=>{
      resolve();
    },ms);
  }); 
}

function platformAlert(text) {
  if (Platform.OS === 'web') {
    alert(text)
  }
}