import React, { Component } from 'react';
import {
  Text,
  View,
  Button,
  ActivityIndicator,
  TextInput
} from 'react-native';

const axios = require('axios');
const lib = require('./lib');
const db = require('./db');
const HOST = lib.HOST;

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      // role:"supplier",
      email:"",
      name:"",
      show:"busy"
    }
  }

  componentDidMount() {
    db.autoLogin()
    .then(profile=>{
      this.setState({
        show:'form'
      });  
      
      if(profile) {
        this.props.history.push('/supplier');
      }
    })
    .catch(err=>{
      lib.alert('db error');
      this.setState({
        show:'form'
      });
    })
  }

  nextPage() {

  }

  onClickTracker() {
    this.props.history.push('/tracker');
  }

  onClickLogin() {
    this.setState({
      show:'busy'
    });

    // check email matching role, if no user register
    const email = this.state.email;
    const name = this.state.name;

    this.getSupplier(email)
    .then(supplier=>{
      if(!supplier) {
        return this.postSupplier(email,name)
          .then(()=>{
            return this.getSupplier(email);
          });
      }

      if(supplier.name !== name) {
        const err = {};
        err.msg = "Email and name mismatch!"; 
        throw err;
      }

      return supplier;
    })
    .then(supplier=>{
      if(!supplier) {
        const err = {};
        err.msg = "Connection error!"; 
        throw err;
      }

      return db.login(email,supplier);
    })
    .then(result=>{
      this.setState({
        show:'form'
      });
      this.props.history.push('/supplier');
    })
    .catch(err=>{
      if(err && err.msg) lib.alert(err.msg);
      this.setState({
        show:'form'
      });
    });
  }

  getSupplier(email) {
    const url = HOST+'/api/Supplier/'+encodeURIComponent(email);
    return new Promise((resolve,reject)=>{
      axios.get(url)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => {
        console.error(error);
        resolve(null);
      })  
    });
  }

  postSupplier(email,name) {
    const url = HOST+'/api/Supplier';
    return new Promise((resolve,reject)=>{
      axios.post(url,{
        "$class": "com.altermyth.mynetwork.Supplier",
        "email": email,
        "name": name
      })
      .then(response => {
        resolve();
      })
      .catch(error => {
        resolve();
      })  
    });
  }

  render() {
    if(this.state.show === "busy") return <View style={{flex:1,alignItems:'center',justifyContent:'center'}}><ActivityIndicator /></View>;

    const btnStr = "LOGIN AS SUPPLIER";
    const btnDisable = (this.state.email.length === 0);

    return (
      <View style={{flex:1}}>
        <View style={{flex:1,padding:10,alignItems:'center',justifyContent:'center'}}>
          <Text style={{textAlign:'center'}}>
            FISH NETWORK
          </Text>
        </View>
        <View style={{padding:20}}>
          <View style={{flexDirection:"row",alignItems:'center'}}>
            <Text style={{flex:1}}>Name: </Text>
            <TextInput
              style={{flex:3,height: 40, borderColor: 'gray', borderWidth: 1}}
              onChangeText={(text) => this.setState({name:text})}
              value={this.state.name}
            />
          </View>
          <View style={{height:10}} />
          <View style={{flexDirection:"row",alignItems:'center'}}>
            <Text style={{flex:1}}>Email: </Text>
            <TextInput
              style={{flex:3,height: 40, borderColor: 'gray', borderWidth: 1}}
              onChangeText={(text) => this.setState({email:text.toLowerCase()})}
              value={this.state.email}
            />
          </View>
          <View style={{height:20}} />
          <Button disabled={btnDisable} title={btnStr.toUpperCase()} onPress={()=>this.onClickLogin()} />
          <View style={{height:20}} />
          <View style={{alignItems:"center"}}><Text>or</Text></View>
          <View style={{height:20}} />
          <Button title="OPEN FISH TRACKER" onPress={()=>this.onClickTracker()} />
        </View>
      </View>
    );
  }
}

