import React, { Component } from 'react';
import {
  Text,
  View,
  Button
} from 'react-native';

const db = require('./db');

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  doLogout() {
    db.logout()
    .then(()=>{
      this.props.history.goBack();
    });
  }

  render() {
    return (
      <View style={{flexDirection:"row",paddingHorizontal:5,paddingVertical:10,borderBottomWidth:1}}>
        <View style={{flexDirection:"column",flex:1}}>
          <Text style={{fontWeight:'bold'}}>{this.props.title}</Text>
          <Text>{this.props.email}</Text>
        </View>
        <View>
          <Button style={{flex:1}} title="EXIT" onPress={()=>this.doLogout()} color="gray"/>
        </View>
      </View>
    );
  }

}