import React, { Component } from 'react';
import {
  Text,
  View,
  Button
} from 'react-native';

const QRCode = require('qrcode.react');

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      height:0,
      size:0
    }
  }

  componentDidMount() {
  }

  onClickBack() {
    this.props.history.goBack();
  }

  render() {
    if(this.state.size === 0)
      return (
        <View style={{flex:1}} onLayout={(event=>{
          const {width,height} = event.nativeEvent.layout;

          let size = width;
          if(size > height) size = height;
          this.setState({size});
        })}>
        </View>
      );
    
    const sz = Math.floor(this.state.size * 0.8);
    // const email = this.props.location.state.email; 
    const show = this.props.location.state.fishCode;

    return (
      <View style={{flex:1}}>
        <View style={{flex:1,alignItems:"center",justifyContent:"center"}} >
          <QRCode value={show} renderAs='svg' size={sz}/>
        </View>
        <View style={{flexDirection:'row',padding:5,alignItems:"center",justifyContent:"center"}}>
          <Text style={{flex:1}}>Code: {show}</Text>
          <View style={{width:10}} />
          <View style={{flex:1}}>
            <Button title="CLOSE" onPress={()=>this.onClickBack()} />
          </View>
        </View>
      </View>
    );
  }
}