import React, { Component } from 'react';
import {
  Text,
  View,
  Button,
  FlatList
} from 'react-native';

const db = require('./db');

export default class Screen extends Component {
  constructor() {
    super();
    this.state = {
      height:0
    }
  }

  componentDidMount() {
    db.getProfile()
    .then(profile => {
      this.setState({email:profile.email});
    });
  }

  renderRows(rows) {
    let btnText = "SHOW QRCODE";
    if(this.props.mode === "supplier") btnText = "SELL FISH";



    return (
      <FlatList
        style={{flex:1}}
        data={rows}
        keyExtractor={(item,index)=>(''+item.id)}
        ItemSeparatorComponent={()=><View style={{height:1,backgroundColor:"gainsboro"}} />}
        renderItem={({item,index}) => {

          let btn = (
            <View style={{alignItems:"center",justifyContent:"center"}}>
              <Button title={btnText} onPress={()=>this.props.onClickQrCode(item.obj)} />
            </View>
          );
    
          if(this.props.mode === "tracker") btn = null;

          return (
            <View key={index} style={{paddingHorizontal:5,paddingVertical:10}}>
              <View style={{flexDirection:"row"}}>
                <View style={{flex:1}}>
                  <Text>{item.row1}</Text>
                  <Text>{item.row2}</Text>
                  <Text>{item.row3}</Text>
                </View>
                {btn}
              </View>
            </View>
          );
        }}
      />
    );
  }

  render() {
    if(this.state.height === 0)
      return (
        <View style={{flex:1}} onLayout={(event=>{
          const {height} = event.nativeEvent.layout;
          this.setState({height});
        })}>
        </View>
      );
    
    const h = this.state.height;
    const rows = this.props.rows;

    return (
      <View style={{height:h,overflow:'hidden'}} >
        {this.renderRows(rows)}
      </View>
    );
  }
}